html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
html,
body {
  font-size: 10px;
  padding: 0;
  margin: 0;
}
body {
  background-color: #efeff4;
}
@media screen and (min-width: 321px) and (max-width: 375px) {
html {
    font-size: 10px;
}
}
@media screen and (min-width: 376px) and (max-width: 414px) {
html {
    font-size: 10px;
}
}
@media screen and (min-width: 415px) and (max-width: 639px) {
html {
    font-size: 12px;
}
}
@media screen and (min-width: 640px) and (max-width: 719px) {
html {
    font-size: 12px;
}
}
@media screen and (min-width: 720px) {
html {
    font-size: 16px;
}
}
@media screen and (min-width: 750px) and (max-width: 799px) {
html {
    font-size: 23.5px;
}
}
@media screen and (min-width: 800px) and (max-width: 1199px) {
html {
    font-size: 25px;
}
}
@media screen and (min-width: 1200px) {
html {
    font-size: 30px;
}
}
#app {
  font-family: 'Microsoft YaHei', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.loading-img {
  position: absolute;
  top: 20%;
  left: 0;
  width: 100%;
  height: auto;
}
.pic-enter-active {
  -webkit-animation: pic-in 0.5s;
          animation: pic-in 0.5s;
}
.pic-leave-active {
  -webkit-animation: pic-out 0.5s;
          animation: pic-out 0.5s;
}
@-webkit-keyframes pic-in {
0% {
    -webkit-transform: scale(0) rotate3d(360, 360, 360, 360deg);
            transform: scale(0) rotate3d(360, 360, 360, 360deg);
}
50% {
    -webkit-transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
            transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
}
100% {
    -webkit-transform: scale(1) rotate3d(0, 0, 0, 0deg);
            transform: scale(1) rotate3d(0, 0, 0, 0deg);
}
}
@keyframes pic-in {
0% {
    -webkit-transform: scale(0) rotate3d(360, 360, 360, 360deg);
            transform: scale(0) rotate3d(360, 360, 360, 360deg);
}
50% {
    -webkit-transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
            transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
}
100% {
    -webkit-transform: scale(1) rotate3d(0, 0, 0, 0deg);
            transform: scale(1) rotate3d(0, 0, 0, 0deg);
}
}
@-webkit-keyframes pic-out {
0% {
    -webkit-transform: scale(1) rotate3d(0, 0, 0, 0deg);
            transform: scale(1) rotate3d(0, 0, 0, 0deg);
}
50% {
    -webkit-transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
            transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
}
100% {
    -webkit-transform: scale(0) rotate3d(360, 360, 360, 360deg);
            transform: scale(0) rotate3d(360, 360, 360, 360deg);
}
}
@keyframes pic-out {
0% {
    -webkit-transform: scale(1) rotate3d(0, 0, 0, 0deg);
            transform: scale(1) rotate3d(0, 0, 0, 0deg);
}
50% {
    -webkit-transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
            transform: scale(1.5) rotate3d(120, 120, 120, 120deg);
}
100% {
    -webkit-transform: scale(0) rotate3d(360, 360, 360, 360deg);
            transform: scale(0) rotate3d(360, 360, 360, 360deg);
}
}



