
.mj {
  min-height: 100vh;
  background-color: #efeff4;
}
.bounce-enter-active {
  -webkit-animation: bounce-in 0.4s;
          animation: bounce-in 0.4s;
}
.bounce-leave-active {
  -webkit-animation: bounce-out 0.5s;
          animation: bounce-out 0.5s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
.expand-enter-active {
  -webkit-animation: expand-in 0.5s;
          animation: expand-in 0.5s;
}
.expand-leave-active {
  -webkit-animation: expand-out 0.7s;
          animation: expand-out 0.7s;
}
@-webkit-keyframes expand-in {
0% {
    margin-top: 1rem;
    -webkit-transform: scale(0.6) translateX(-1500px);
            transform: scale(0.6) translateX(-1500px);
}
25% {
    margin-top: 1rem;
    -webkit-transform: scale(0.6) translateX(200px);
            transform: scale(0.6) translateX(200px);
}
50% {
    margin-top: 1rem;
    -webkit-transform: scale(0.6) translateX(200px);
            transform: scale(0.6) translateX(200px);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes expand-in {
0% {
    margin-top: 1rem;
    -webkit-transform: scale(0.6) translateX(-1500px);
            transform: scale(0.6) translateX(-1500px);
}
25% {
    margin-top: 1rem;
    -webkit-transform: scale(0.6) translateX(200px);
            transform: scale(0.6) translateX(200px);
}
50% {
    margin-top: 1rem;
    -webkit-transform: scale(0.6) translateX(200px);
            transform: scale(0.6) translateX(200px);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes expand-out {
0% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6);
            transform: scaleX(0.8) scaleY(0.6);
}
9% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6);
            transform: scaleX(0.8) scaleY(0.6);
}
10% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(100px);
            transform: scaleX(0.8) scaleY(0.6) translateX(100px);
}
25% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(200px);
            transform: scaleX(0.8) scaleY(0.6) translateX(200px);
}
50% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(250px);
            transform: scaleX(0.8) scaleY(0.6) translateX(250px);
}
75% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(-1000px);
            transform: scaleX(0.8) scaleY(0.6) translateX(-1000px);
}
100% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0) translateX(-1000px);
            transform: scaleX(0.8) scaleY(0) translateX(-1000px);
}
}
@keyframes expand-out {
0% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6);
            transform: scaleX(0.8) scaleY(0.6);
}
9% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6);
            transform: scaleX(0.8) scaleY(0.6);
}
10% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(100px);
            transform: scaleX(0.8) scaleY(0.6) translateX(100px);
}
25% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(200px);
            transform: scaleX(0.8) scaleY(0.6) translateX(200px);
}
50% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(250px);
            transform: scaleX(0.8) scaleY(0.6) translateX(250px);
}
75% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0.6) translateX(-1000px);
            transform: scaleX(0.8) scaleY(0.6) translateX(-1000px);
}
100% {
    margin-top: 1rem;
    -webkit-transform: scaleX(0.8) scaleY(0) translateX(-1000px);
            transform: scaleX(0.8) scaleY(0) translateX(-1000px);
}
}



