
.article {
  background-color: white;
  padding: 2rem;
}
.article-title {
  text-align: center;
  padding: 1rem;
  font-size: 2.1rem;
}
.article-content {
  padding-bottom: 1rem;
  text-align: justify;
  font-size: 1.6rem;
}
.article-content:last-child {
  padding-bottom: 0;
}
.article-content a {
  color: #2c3e50;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.show-important {
  display: block!important;
}
.clearfixed:after {
  content: '';
  display: block;
  clear: both;
}
.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
              -ms-flex-align: center;
          align-items: center;
}
.btg {
  border-bottom: 1px solid #dedede;
}
.btopg {
  border-top: 1px solid #dedede;
}
.officeimg {
  text-align: center;
  font-size: 0;
  padding: 0.4rem 0.6rem;
}
.officeimg img {
  width: 100%;
  max-width: 305px;
  height: auto;
  border: 1px solid #dedede;
  box-sizing: border-box;
  border-radius: 2px;
}
.office-detail {
  padding: 0 0.6rem;
}
.office-detail p {
  font-size: 1.4rem;
}
.youku {
  padding: 1rem;
}
.youku a {
  font-size: 1.3rem;
  padding: .3rem;
  border: none;
  background: #ff6d40;
  color: white;
}
.addaddress {
  min-height: 100vh;
  background-color: #efeff4;
}
.addaddress-container .weui-cell__hd .weui-label {
  font-size: 1.5rem;
  text-align: left;
}
.addaddress-container .weui-cell__bd.weui-cell__primary input {
  text-align: left;
  font-size: 1.5rem;
}
.addaddress-container .title {
  padding: 10px 15px;
  -webkit-box-pack: justify;
              -ms-flex-pack: justify;
          justify-content: space-between;
  font-size: 1.5rem;
  border-bottom: 1px solid #D9D9D9;
}
.addaddress-container .title-info {
  color: #ff5c5c;
  font-size: 1.2rem;
}
.addaddress-container .title-add {
  color: #999;
}
.addaddress-container-add {
  background: white;
  bottom: 22px;
  position: fixed;
  width: 100%;
  text-align: center;
}
.addaddress-container-add p {
  float: right;
  text-align: center;
  font-size: 1.8rem;
  padding: .5rem 0;
  width: 20%;
  color: white;
  background: #3462ff;
}
.addaddress-container-add p:after {
  clear: both;
}



